@import "styles/mixins";

.wrapper {
  display: flex;
  flex-direction: column;
  margin: 3rem auto;
  min-height: calc(100vh - var(--header-height));
}

.spinner {
  margin: auto;
  width: 8rem;
  stroke: color(white)
}

.heading {
  span {
    font-size: 1.6rem;
    vertical-align: super;
  }
}

.filter {
  margin-top: 4rem;
}

.empty {
  @include fontRegular(color(white));

  text-align: center;
}
